@import url('https://fonts.googleapis.com/css2?family=M+PLUS+1+Code&display=swap');

    *{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }
    html{
        scroll-behavior:smooth;
    }

    html, body{
        min-height: 300vh;
    }

    body{
        /*min-height: calc(3000px + 100vh);*/
        font-family: 'M PLUS 1 Code', sans-serif;
    }

    #video1{
        display: none;
    }

    .webgl{
        position: fixed;
        top: 0;
        left: 0;
        outline: none;
    }


/* HTML NORMAL */

    .contenedor{
        position: absolute;
        z-index: 1;
        width: 100%;
        min-height: 300vh;
        display: grid;
        place-content: left;
        transition: 150ms all ease-in;
    }

    .contenido{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

/* Navegacion Y RRSS */
    .barra{
        height: 100vh;
        position: fixed;
        /*max-width: 2vw;*/
        top: 0;
        right: 1%;
        margin: 0;
        padding: 0;
        display: flex;
        flex-direction: column;
        justify-content:space-between;
        align-items: center;
    }

    /*Navegacion*/
        .navegation{
            /*transform: rotate(-90deg) translateX(28px);*/
            margin: 5vh 0 0 0;
        }

        .navegation a {
            font-size: 1.5vw;
            color: #ffc872;
            display: inline-block;
            padding: 0.7vh;
            text-decoration: none;
            cursor: pointer;
            opacity: .5;
            transform: rotate(-90deg);
            transition: 100ms all ease-in-out;
        }

        .navegation li a:hover {
            opacity: 1;
            color: #7ace67;
            transform: scale(1.1);
        }

        .navegation ul li{
            list-style: none;
            padding: 0;
            display: flex;
            flex-direction: column;
        }

    /* Footer */
        .footer{
            /*transform: rotate(-90deg) translateX(28px);*/
            margin: 0 0 5vh 0;
            padding: 0;
        }

        .footer a {
            font-size: 1.5vw;
            color: #ffc872;
            display: inline-block;
            padding: 0.7vh;
            text-decoration: none;
            cursor: pointer;
            opacity: .5;
            transform: rotate(-90deg);
            transition: 100ms all ease-in-out;
        }

        .footer li a:hover {
            opacity: 1;
            color: #fd676c;
            transform: scale(1.1);
        }

        .footer ul li{
            list-style: none;
            padding: 0;
            display: flex;
            flex-direction: column;
        }

/* Header */

    .header{
        min-height: 100vh;
        padding: 0 3%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        transition: 150ms all ease-in;
        /*background: rgba(0, 0, 0, 0.5);*/
        /*border-right: 3px white solid;*/
    }

        .header h1{
            color: #ffe3b3;
            font-size: 6vw;
        }

        .header h2{
            color: #ffe3b3;
            font-size: 3.5vw;
        }

        .header h2 .auto-input{
            color: #7ace67;
        }

/* Work */
    .work{
        width: 100%;
        min-height: 100vh;
        padding: 0 2%;
        display: flex;
        flex-direction:column;
        justify-content: space-around;
    }

    .work h2{
        font-size: 5vw;
        color: #7ace67;
    }

    .work #SubTitle{
        color: #ffe3b3;
        font-size: 1vw;
        opacity: 0.5;
        transform: translateX(33px);
    }
    .work .SubWork{
        max-width: 90%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: center;
        align-items: left;
    }
    .work .SubWork #bola2{
        width: 1vw;
        height: 1vw;
        opacity: 0.5;
        background: #fd676c;
        border-top-right-radius: 50%;
        border-bottom-right-radius: 50%;
        transform: translateX(20px) translateY(-1px);
        transition: 150ms all;
    }
    .work .SubWork:hover #SubTitle{
        opacity: 0.8;
    }
    .work .SubWork:hover #bola2{
        opacity: 1;
        border-top-left-radius: 50%;
        border-bottom-left-radius: 50%;
    }

    .work #iconoMenu{
        font-size: 3.5vw;
        color: #7ace67;
        margin-left: 20px;
        margin-top: 7px;
    }

    .work ul li{
        list-style: none;
        margin-bottom: 25px;
    }
    .work ul{
        transform: translateX(-10px);
    }

    .titleDiv{
        display: flex;
        flex-direction:row;
        justify-content: flex-start;
        align-items: center;
        margin: 0 0 2vh 0;
    }
    .SubTitleDiv{
        display: flex;
        flex-direction:row;
        justify-content: flex-start;
        align-items: center;
        margin: 0 0 5vh 0;
    }

    .work #gradient{
        width: 10px;
        height: 10px;
        margin-left: 0;
        margin-top: 0;
        margin-right: 15px;
        background: linear-gradient(rgba(0,255,0,0), #7ace67);
        transform: rotate(90deg) scaleY(0) scaleX(5);
        transition: 200ms all ease-in-out;
    }

    .work .SubWork:hover #gradient{
        margin-right: 95px;
        transform: rotate(90deg) translateY(-45px) scaleY(10)  scaleX(5);
    }
    
    .work .SubLab:hover #gradient{
        margin-right: 95px;
        transform: rotate(90deg) translateX(2.25rem) translateY(-45px) scaleY(10)  scaleX(5);
    }

    /* LIs */

        #gradienteBlock{
            width: 30px;
            height: 10px;
            margin-left: 0;
            margin-top: 0;
            margin-right: 15px;
            background: linear-gradient(rgba(0,255,0,0), #7ace67);
            transform: rotate(90deg) scaleY(0) scaleX(1);
            transition: 200ms all ease-in-out;
        }
        .work a:hover #gradienteBlock{
            margin-right: 90px;
            transform: rotate(90deg) translateY(-45px) scaleY(10)  scaleX(1);
        }


        .linkContenido{
            display: flex;
            flex-direction: row;
            align-items: center;
        }
        .linkContenido h3{
            color: #ffe3b3;
            font-size: 1.8vw;
            opacity: 0.5;
            transition: 150ms all;
        }
        .linkContenido #bola{
            width: 0.7vw;
            height: 0.7vw;
            background: #fd676c;
            border-top-left-radius: 50%;
            border-bottom-left-radius: 50%;
            margin-left: 1.4vw;
            transition: 150ms all;
        }
        .linkContenido h4{
            color: #ffe3b3;
            font-size: 1vw;
            margin-left: 1.4vw;
            opacity: 0.4;
            transition: 150ms all;
        }

        .work a:hover .linkContenido h3{
            opacity: 1;
        }
        .work a:hover .linkContenido h4{
            opacity: 0.8;
            color: #7ace67;
        }
        .work a:hover .linkContenido #bola{
            border-radius: 50%;
        }


/* About */
    .about{
        width: 100%;
        min-height: 100vh;
        padding: 0 3%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .about h2{
        font-size: 4vw;
        color: #7ace67;
    }

    .about p{
        color: #ffe3b3;
        font-size: 1.3vw;
        max-width: 40%;
    }

