    @media screen and (max-width: 700px) { /* Pantalla Pequeña */
        .barra{
            justify-content: space-around;
        }
        .navegation{
            margin: 0;
        }
        .footer{
            margin: 0;
        }

        /**/
        
        .header h1{
            font-size: 11vw;
        }
        .header h2{
            font-size: 6vw;
        }

        /**/

        .navegation a{
            font-size: 6vw;
        }
        .footer a{
            font-size: 6vw;
        }

        /**/

        .work h2{
            font-size: 10vw;
        }
        .work #iconoMenu{
            font-size: 8vw;
        }
        .work .SubWork #bola2{
            width: 1.1vw;
            height: 1.1vh;
        }
        .work #SubTitle{
            font-size: 3vw;
        }

        /**/

        .linkContenido h3{
            font-size: 4.5vw;
        }
        .linkContenido #bola{
            width: 1.4vw;
            height: 1.4vh;
        }
        .linkContenido h4{
            font-size: 2.5vw;
        }

        /**/

        .about h2{
            font-size: 10vw;
        }
    
        .about p{
            font-size: 3.4vw;
            max-width: 70%;
        }
    }

    @media screen and (max-height: 280px) { /* Pantalla Estrecha */
        .footer{
            display: none;
        }
    }